/**
 * CaseReportPage - Report builder for a specific case
 *
 * Allows lawyers to build and export reports from case data
 */

import React, { useMemo } from 'react';
import { useParams, Link } from 'react-router-dom';
import { useQuery } from '@tanstack/react-query';
import { ArrowRight, FileText, Loader2 } from 'lucide-react';
import { toast } from 'sonner';
import { apiGet } from '../utils/api';
import { ReportBuilder } from '../components/report';
import type { ReportData } from '../components/report';
import type { Contradiction } from '../types';

export function CaseReportPage() {
  const { caseId } = useParams<{ caseId: string }>();

  // Fetch case data
  const { data: caseData, isLoading } = useQuery({
    queryKey: ['case', caseId],
    queryFn: () => apiGet(`/api/cases/${caseId}`),
    enabled: !!caseId,
  });

  // Convert contradictions to the format expected by ReportBuilder
  const contradictions = useMemo<Contradiction[]>(() => {
    if (!caseData?.contradictions) return [];

    return (caseData.contradictions || []).map((c: any, idx: number) => ({
      id: c.id || `contradiction-${idx}`,
      claim_a: c.statement_a || c.claim_a || c.text_a || '',
      claim_b: c.statement_b || c.claim_b || c.text_b || '',
      severity: c.severity || 'medium',
      type: c.type || c.contradiction_type || 'general',
      sources: [c.source_a, c.source_b].filter(Boolean),
      explanation: c.explanation || c.description || '',
    }));
  }, [caseData]);

  // Handle save
  const handleSave = (report: ReportData) => {
    // In a real implementation, this would save to the backend
    console.log('Saving report:', report);
    toast.success('הדוח נשמר בהצלחה');

    // Store in localStorage for now
    try {
      const reports = JSON.parse(localStorage.getItem('jethro-reports') || '{}');
      reports[report.id] = report;
      localStorage.setItem('jethro-reports', JSON.stringify(reports));
    } catch (err) {
      console.error('Failed to save report to localStorage:', err);
    }
  };

  // Handle export
  const handleExport = (format: 'pdf' | 'print' | 'docx') => {
    if (format === 'print') {
      window.print();
      return;
    }

    toast.info(`ייצוא ל-${format.toUpperCase()} יהיה זמין בקרוב`);
  };

  if (isLoading) {
    return (
      <div className="h-full flex items-center justify-center bg-slate-950">
        <Loader2 className="w-8 h-8 text-blue-400 animate-spin" />
      </div>
    );
  }

  if (!caseId) {
    return (
      <div className="h-full flex items-center justify-center bg-slate-950">
        <p className="text-white/60">לא נמצא תיק</p>
      </div>
    );
  }

  return (
    <div className="h-full flex flex-col bg-slate-950" dir="rtl">
      {/* Header */}
      <div className="px-6 py-4 border-b border-slate-800 bg-slate-900">
        <div className="flex items-center gap-4">
          <Link
            to={`/cases/${caseId}`}
            className="p-2 text-white/60 hover:text-white hover:bg-slate-800 rounded-lg transition-colors"
          >
            <ArrowRight className="w-5 h-5" />
          </Link>
          <div className="flex items-center gap-3">
            <div className="p-2 bg-blue-600/20 rounded-lg">
              <FileText className="text-blue-400 w-5 h-5" />
            </div>
            <div>
              <h1 className="text-lg font-semibold text-white">בניית דוח</h1>
              <p className="text-sm text-white/50">
                {caseData?.name || caseData?.case_name || 'תיק'}
              </p>
            </div>
          </div>
        </div>
      </div>

      {/* Report Builder */}
      <div className="flex-1 overflow-hidden">
        <ReportBuilder
          caseId={caseId}
          caseName={caseData?.name || caseData?.case_name || 'תיק'}
          contradictions={contradictions}
          onSave={handleSave}
          onExport={handleExport}
        />
      </div>
    </div>
  );
}

export default CaseReportPage;
