/**
 * Contradiction Workbench Store
 *
 * Manages contradiction triage, status tracking, and assignments
 * Persisted to localStorage for offline work
 */

import { create } from 'zustand';
import { persist } from 'zustand/middleware';
import type {
  ContradictionWorkbenchStatus,
  ContradictionWorkbenchItem,
  ContradictionAssignment,
} from '../types';

// ===========================
// Types
// ===========================

interface WorkbenchFilters {
  status: ContradictionWorkbenchStatus | 'all';
  severity: 'critical' | 'high' | 'medium' | 'low' | 'all';
  type: string | 'all';
  assignee: string | 'all';
  sortBy: 'severity' | 'status' | 'updatedAt' | 'type';
  sortOrder: 'asc' | 'desc';
}

interface ContradictionWorkbenchState {
  // Items by case ID
  itemsByCaseId: Record<string, Record<string, ContradictionWorkbenchItem>>;

  // Filters
  filters: WorkbenchFilters;

  // Report builder items
  reportItems: Record<string, string[]>; // caseId -> contradictionIds

  // Actions
  setStatus: (
    caseId: string,
    contradictionId: string,
    status: ContradictionWorkbenchStatus
  ) => void;

  setAssignment: (
    caseId: string,
    contradictionId: string,
    assignment: ContradictionAssignment | undefined
  ) => void;

  completeAssignment: (caseId: string, contradictionId: string) => void;

  addToReport: (caseId: string, contradictionId: string) => void;
  removeFromReport: (caseId: string, contradictionId: string) => void;
  clearReport: (caseId: string) => void;

  setFilter: <K extends keyof WorkbenchFilters>(
    key: K,
    value: WorkbenchFilters[K]
  ) => void;
  resetFilters: () => void;

  // Selectors
  getItem: (
    caseId: string,
    contradictionId: string
  ) => ContradictionWorkbenchItem | undefined;

  getItemsForCase: (caseId: string) => ContradictionWorkbenchItem[];
  getReportItems: (caseId: string) => string[];
  getStats: (caseId: string) => {
    total: number;
    new: number;
    confirmed: number;
    notContradiction: number;
    needsEvidence: number;
    usedInReport: number;
    assigned: number;
  };

  // Reset
  reset: () => void;
  resetCase: (caseId: string) => void;
}

// ===========================
// Initial State
// ===========================

const initialFilters: WorkbenchFilters = {
  status: 'all',
  severity: 'all',
  type: 'all',
  assignee: 'all',
  sortBy: 'severity',
  sortOrder: 'desc',
};

// ===========================
// Store
// ===========================

export const useContradictionWorkbenchStore = create<ContradictionWorkbenchState>()(
  persist(
    (set, get) => ({
      itemsByCaseId: {},
      filters: { ...initialFilters },
      reportItems: {},

      setStatus: (caseId, contradictionId, status) =>
        set((state) => {
          const caseItems = state.itemsByCaseId[caseId] || {};
          const existing = caseItems[contradictionId];

          return {
            itemsByCaseId: {
              ...state.itemsByCaseId,
              [caseId]: {
                ...caseItems,
                [contradictionId]: {
                  contradictionId,
                  status,
                  assignment: existing?.assignment,
                  addedToReport: existing?.addedToReport || false,
                  updatedAt: new Date().toISOString(),
                },
              },
            },
          };
        }),

      setAssignment: (caseId, contradictionId, assignment) =>
        set((state) => {
          const caseItems = state.itemsByCaseId[caseId] || {};
          const existing = caseItems[contradictionId];

          return {
            itemsByCaseId: {
              ...state.itemsByCaseId,
              [caseId]: {
                ...caseItems,
                [contradictionId]: {
                  contradictionId,
                  status: existing?.status || 'new',
                  assignment,
                  addedToReport: existing?.addedToReport || false,
                  updatedAt: new Date().toISOString(),
                },
              },
            },
          };
        }),

      completeAssignment: (caseId, contradictionId) =>
        set((state) => {
          const caseItems = state.itemsByCaseId[caseId] || {};
          const existing = caseItems[contradictionId];
          if (!existing?.assignment) return state;

          return {
            itemsByCaseId: {
              ...state.itemsByCaseId,
              [caseId]: {
                ...caseItems,
                [contradictionId]: {
                  ...existing,
                  assignment: {
                    ...existing.assignment,
                    completedAt: new Date().toISOString(),
                  },
                  updatedAt: new Date().toISOString(),
                },
              },
            },
          };
        }),

      addToReport: (caseId, contradictionId) =>
        set((state) => {
          const caseItems = state.itemsByCaseId[caseId] || {};
          const existing = caseItems[contradictionId];
          const reportIds = state.reportItems[caseId] || [];

          return {
            itemsByCaseId: {
              ...state.itemsByCaseId,
              [caseId]: {
                ...caseItems,
                [contradictionId]: {
                  contradictionId,
                  status: existing?.status || 'new',
                  assignment: existing?.assignment,
                  addedToReport: true,
                  updatedAt: new Date().toISOString(),
                },
              },
            },
            reportItems: {
              ...state.reportItems,
              [caseId]: reportIds.includes(contradictionId)
                ? reportIds
                : [...reportIds, contradictionId],
            },
          };
        }),

      removeFromReport: (caseId, contradictionId) =>
        set((state) => {
          const caseItems = state.itemsByCaseId[caseId] || {};
          const existing = caseItems[contradictionId];
          const reportIds = state.reportItems[caseId] || [];

          return {
            itemsByCaseId: {
              ...state.itemsByCaseId,
              [caseId]: {
                ...caseItems,
                [contradictionId]: {
                  ...existing,
                  contradictionId,
                  status: existing?.status || 'new',
                  addedToReport: false,
                  updatedAt: new Date().toISOString(),
                },
              },
            },
            reportItems: {
              ...state.reportItems,
              [caseId]: reportIds.filter((id) => id !== contradictionId),
            },
          };
        }),

      clearReport: (caseId) =>
        set((state) => ({
          reportItems: {
            ...state.reportItems,
            [caseId]: [],
          },
        })),

      setFilter: (key, value) =>
        set((state) => ({
          filters: { ...state.filters, [key]: value },
        })),

      resetFilters: () => set({ filters: { ...initialFilters } }),

      getItem: (caseId, contradictionId) => {
        return get().itemsByCaseId[caseId]?.[contradictionId];
      },

      getItemsForCase: (caseId) => {
        const items = get().itemsByCaseId[caseId] || {};
        return Object.values(items);
      },

      getReportItems: (caseId) => {
        return get().reportItems[caseId] || [];
      },

      getStats: (caseId) => {
        const items = Object.values(get().itemsByCaseId[caseId] || {});
        return {
          total: items.length,
          new: items.filter((i) => i.status === 'new').length,
          confirmed: items.filter((i) => i.status === 'confirmed').length,
          notContradiction: items.filter((i) => i.status === 'not_contradiction').length,
          needsEvidence: items.filter((i) => i.status === 'needs_evidence').length,
          usedInReport: items.filter((i) => i.status === 'used_in_report').length,
          assigned: items.filter((i) => i.assignment && !i.assignment.completedAt).length,
        };
      },

      reset: () =>
        set({
          itemsByCaseId: {},
          filters: { ...initialFilters },
          reportItems: {},
        }),

      resetCase: (caseId) =>
        set((state) => {
          const { [caseId]: _, ...rest } = state.itemsByCaseId;
          const { [caseId]: __, ...restReport } = state.reportItems;
          return {
            itemsByCaseId: rest,
            reportItems: restReport,
          };
        }),
    }),
    {
      name: 'jethro-workbench',
      partialize: (state) => ({
        itemsByCaseId: state.itemsByCaseId,
        reportItems: state.reportItems,
      }),
    }
  )
);

// ===========================
// Convenience Hooks
// ===========================

export const useWorkbenchItem = (
  caseId: string,
  contradictionId: string
): ContradictionWorkbenchItem | undefined => {
  return useContradictionWorkbenchStore((state) =>
    state.itemsByCaseId[caseId]?.[contradictionId]
  );
};

export const useWorkbenchStats = (caseId: string) => {
  return useContradictionWorkbenchStore((state) => state.getStats(caseId));
};

export default useContradictionWorkbenchStore;
