/**
 * ReportBuilder - Build and export legal reports
 *
 * Features:
 * - Select contradictions and evidence for report
 * - Reorder and organize sections
 * - Preview report layout
 * - Export to PDF or print
 */

import React, { useState, useMemo, useCallback } from 'react';
import {
  FileText,
  Plus,
  Trash2,
  GripVertical,
  Download,
  Printer,
  Eye,
  EyeOff,
  ChevronDown,
  ChevronUp,
  Save,
  FolderOpen,
  AlertTriangle,
  FileCheck,
} from 'lucide-react';
import { useContradictionWorkbenchStore } from '../../store/useContradictionWorkbenchStore';
import type { Contradiction, ContradictionWorkbenchStatus } from '../../types';

// ===========================
// Types
// ===========================

export type ReportSectionType = 'summary' | 'contradiction' | 'evidence' | 'claim' | 'custom';

export interface ReportSection {
  id: string;
  type: ReportSectionType;
  title: string;
  content: string;
  referenceId?: string; // ID of contradiction/evidence/claim
  order: number;
  isVisible: boolean;
}

export interface ReportData {
  id: string;
  title: string;
  caseId: string;
  caseName: string;
  createdAt: string;
  updatedAt: string;
  sections: ReportSection[];
}

interface ReportBuilderProps {
  caseId: string;
  caseName: string;
  contradictions: Contradiction[];
  onExport?: (format: 'pdf' | 'print' | 'docx') => void;
  onSave?: (report: ReportData) => void;
  className?: string;
}

// ===========================
// Constants
// ===========================

const SECTION_TYPE_CONFIG: Record<
  ReportSectionType,
  { label: string; icon: React.ElementType; color: string }
> = {
  summary: {
    label: 'סיכום',
    icon: FileText,
    color: 'text-blue-400',
  },
  contradiction: {
    label: 'סתירה',
    icon: AlertTriangle,
    color: 'text-amber-400',
  },
  evidence: {
    label: 'ראיה',
    icon: FileCheck,
    color: 'text-green-400',
  },
  claim: {
    label: 'טענה',
    icon: FileText,
    color: 'text-purple-400',
  },
  custom: {
    label: 'מותאם אישית',
    icon: FileText,
    color: 'text-gray-400',
  },
};

// ===========================
// Section Editor
// ===========================

const SectionEditor: React.FC<{
  section: ReportSection;
  onUpdate: (section: ReportSection) => void;
  onDelete: () => void;
  onMoveUp: () => void;
  onMoveDown: () => void;
  isFirst: boolean;
  isLast: boolean;
}> = ({ section, onUpdate, onDelete, onMoveUp, onMoveDown, isFirst, isLast }) => {
  const [isExpanded, setIsExpanded] = useState(true);
  const config = SECTION_TYPE_CONFIG[section.type];
  const Icon = config.icon;

  return (
    <div
      className={`
        border rounded-lg overflow-hidden
        ${section.isVisible ? 'border-slate-600' : 'border-slate-700 opacity-60'}
      `}
    >
      {/* Header */}
      <div className="flex items-center gap-2 px-3 py-2 bg-slate-800/50">
        {/* Drag handle */}
        <GripVertical className="w-4 h-4 text-white/30 cursor-grab" />

        {/* Type icon */}
        <Icon className={`w-4 h-4 ${config.color}`} />

        {/* Title */}
        <input
          type="text"
          value={section.title}
          onChange={(e) => onUpdate({ ...section, title: e.target.value })}
          className="flex-1 bg-transparent text-sm font-medium text-white focus:outline-none"
          placeholder="כותרת הקטע"
        />

        {/* Actions */}
        <div className="flex items-center gap-1">
          {/* Visibility toggle */}
          <button
            onClick={() => onUpdate({ ...section, isVisible: !section.isVisible })}
            className="p-1 text-white/40 hover:text-white hover:bg-slate-700 rounded transition-colors"
            title={section.isVisible ? 'הסתר' : 'הצג'}
          >
            {section.isVisible ? (
              <Eye className="w-3.5 h-3.5" />
            ) : (
              <EyeOff className="w-3.5 h-3.5" />
            )}
          </button>

          {/* Move up */}
          <button
            onClick={onMoveUp}
            disabled={isFirst}
            className="p-1 text-white/40 hover:text-white hover:bg-slate-700 rounded transition-colors disabled:opacity-30 disabled:cursor-not-allowed"
            title="הזז למעלה"
          >
            <ChevronUp className="w-3.5 h-3.5" />
          </button>

          {/* Move down */}
          <button
            onClick={onMoveDown}
            disabled={isLast}
            className="p-1 text-white/40 hover:text-white hover:bg-slate-700 rounded transition-colors disabled:opacity-30 disabled:cursor-not-allowed"
            title="הזז למטה"
          >
            <ChevronDown className="w-3.5 h-3.5" />
          </button>

          {/* Expand/collapse */}
          <button
            onClick={() => setIsExpanded(!isExpanded)}
            className="p-1 text-white/40 hover:text-white hover:bg-slate-700 rounded transition-colors"
          >
            <ChevronDown
              className={`w-3.5 h-3.5 transition-transform ${
                isExpanded ? 'rotate-180' : ''
              }`}
            />
          </button>

          {/* Delete */}
          <button
            onClick={onDelete}
            className="p-1 text-white/40 hover:text-red-400 hover:bg-slate-700 rounded transition-colors"
            title="מחק"
          >
            <Trash2 className="w-3.5 h-3.5" />
          </button>
        </div>
      </div>

      {/* Content */}
      {isExpanded && (
        <div className="p-3 bg-slate-900/50">
          <textarea
            value={section.content}
            onChange={(e) => onUpdate({ ...section, content: e.target.value })}
            className="w-full min-h-[100px] p-3 bg-slate-800 border border-slate-700 rounded-lg text-sm text-white/80 resize-y focus:outline-none focus:ring-1 focus:ring-blue-500"
            placeholder="תוכן הקטע..."
            dir="rtl"
          />
        </div>
      )}
    </div>
  );
};

// ===========================
// Add Section Menu
// ===========================

const AddSectionMenu: React.FC<{
  onAdd: (type: ReportSectionType) => void;
  contradictions: Contradiction[];
  reportContradictionIds: Set<string>;
  onAddContradiction: (contradiction: Contradiction) => void;
}> = ({ onAdd, contradictions, reportContradictionIds, onAddContradiction }) => {
  const [showMenu, setShowMenu] = useState(false);
  const [showContradictions, setShowContradictions] = useState(false);

  // Filter contradictions not yet in report
  const availableContradictions = contradictions.filter(
    (c) => !reportContradictionIds.has(c.id)
  );

  return (
    <div className="relative">
      <button
        onClick={() => setShowMenu(!showMenu)}
        className="flex items-center gap-2 px-4 py-2 bg-blue-600 hover:bg-blue-500 text-white rounded-lg transition-colors"
      >
        <Plus className="w-4 h-4" />
        <span className="text-sm font-medium">הוסף קטע</span>
      </button>

      {showMenu && (
        <div className="absolute top-full right-0 mt-2 z-20 w-56 bg-slate-800 border border-slate-700 rounded-lg shadow-xl overflow-hidden">
          {/* Section types */}
          {(Object.keys(SECTION_TYPE_CONFIG) as ReportSectionType[]).map((type) => {
            const config = SECTION_TYPE_CONFIG[type];
            const Icon = config.icon;

            return (
              <button
                key={type}
                onClick={() => {
                  if (type === 'contradiction') {
                    setShowContradictions(!showContradictions);
                  } else {
                    onAdd(type);
                    setShowMenu(false);
                  }
                }}
                className="w-full flex items-center gap-3 px-4 py-2.5 text-white/80 hover:bg-slate-700 transition-colors"
              >
                <Icon className={`w-4 h-4 ${config.color}`} />
                <span className="text-sm">{config.label}</span>
                {type === 'contradiction' && availableContradictions.length > 0 && (
                  <span className="mr-auto text-xs text-white/40">
                    ({availableContradictions.length})
                  </span>
                )}
              </button>
            );
          })}

          {/* Contradictions submenu */}
          {showContradictions && availableContradictions.length > 0 && (
            <div className="border-t border-slate-700 max-h-48 overflow-y-auto">
              {availableContradictions.map((c) => (
                <button
                  key={c.id}
                  onClick={() => {
                    onAddContradiction(c);
                    setShowMenu(false);
                    setShowContradictions(false);
                  }}
                  className="w-full px-4 py-2 text-right text-xs text-white/60 hover:bg-slate-700 transition-colors"
                >
                  <div className="truncate">{c.claim_a}</div>
                  <div className="text-amber-400/60 text-[10px]">vs</div>
                  <div className="truncate">{c.claim_b}</div>
                </button>
              ))}
            </div>
          )}
        </div>
      )}
    </div>
  );
};

// ===========================
// Report Preview
// ===========================

const ReportPreview: React.FC<{
  report: ReportData;
  onClose: () => void;
}> = ({ report, onClose }) => {
  const visibleSections = report.sections.filter((s) => s.isVisible);

  return (
    <div className="fixed inset-0 bg-black/80 z-50 flex items-center justify-center p-4">
      <div className="bg-white w-full max-w-3xl max-h-[90vh] overflow-auto rounded-lg shadow-2xl">
        {/* Close button */}
        <div className="sticky top-0 bg-white border-b p-4 flex justify-between items-center">
          <h2 className="text-xl font-bold text-gray-900">{report.title}</h2>
          <button
            onClick={onClose}
            className="px-4 py-2 bg-gray-100 hover:bg-gray-200 text-gray-700 rounded-lg transition-colors"
          >
            סגור תצוגה מקדימה
          </button>
        </div>

        {/* Content */}
        <div className="p-8" dir="rtl">
          {/* Header */}
          <div className="mb-8 pb-4 border-b-2">
            <h1 className="text-2xl font-bold text-gray-900 mb-2">
              {report.title}
            </h1>
            <p className="text-gray-600">תיק: {report.caseName}</p>
            <p className="text-gray-500 text-sm">
              תאריך: {new Date(report.updatedAt).toLocaleDateString('he-IL')}
            </p>
          </div>

          {/* Sections */}
          {visibleSections.map((section, index) => (
            <div key={section.id} className="mb-8">
              <h2 className="text-lg font-semibold text-gray-800 mb-3 flex items-center gap-2">
                <span className="text-gray-400">{index + 1}.</span>
                {section.title}
              </h2>
              <div className="text-gray-700 leading-relaxed whitespace-pre-wrap">
                {section.content || 'אין תוכן'}
              </div>
            </div>
          ))}

          {/* Footer */}
          <div className="mt-12 pt-4 border-t text-center text-gray-400 text-sm">
            נוצר באמצעות יתרו AI
          </div>
        </div>
      </div>
    </div>
  );
};

// ===========================
// Main Component
// ===========================

export const ReportBuilder: React.FC<ReportBuilderProps> = ({
  caseId,
  caseName,
  contradictions,
  onExport,
  onSave,
  className = '',
}) => {
  const { getReportItems } = useContradictionWorkbenchStore();
  const reportContradictionIds = new Set(getReportItems(caseId));

  // Report state
  const [report, setReport] = useState<ReportData>({
    id: `report-${Date.now()}`,
    title: `דוח ניתוח - ${caseName}`,
    caseId,
    caseName,
    createdAt: new Date().toISOString(),
    updatedAt: new Date().toISOString(),
    sections: [
      {
        id: 'summary-1',
        type: 'summary',
        title: 'תקציר מנהלים',
        content: '',
        order: 0,
        isVisible: true,
      },
    ],
  });

  const [showPreview, setShowPreview] = useState(false);

  // Update report
  const updateReport = useCallback((updates: Partial<ReportData>) => {
    setReport((prev) => ({
      ...prev,
      ...updates,
      updatedAt: new Date().toISOString(),
    }));
  }, []);

  // Add section
  const addSection = useCallback(
    (type: ReportSectionType, content?: string, title?: string, referenceId?: string) => {
      const config = SECTION_TYPE_CONFIG[type];
      const newSection: ReportSection = {
        id: `section-${Date.now()}`,
        type,
        title: title || config.label,
        content: content || '',
        referenceId,
        order: report.sections.length,
        isVisible: true,
      };

      updateReport({
        sections: [...report.sections, newSection],
      });
    },
    [report.sections, updateReport]
  );

  // Add contradiction as section
  const addContradictionSection = useCallback(
    (contradiction: Contradiction) => {
      const content = `טענה א׳: ${contradiction.claim_a}\n\nטענה ב׳: ${contradiction.claim_b}\n\n${
        contradiction.explanation ? `הסבר: ${contradiction.explanation}` : ''
      }`;
      addSection('contradiction', content, `סתירה - ${contradiction.type}`, contradiction.id);
    },
    [addSection]
  );

  // Update section
  const updateSection = useCallback(
    (sectionId: string, updates: Partial<ReportSection>) => {
      updateReport({
        sections: report.sections.map((s) =>
          s.id === sectionId ? { ...s, ...updates } : s
        ),
      });
    },
    [report.sections, updateReport]
  );

  // Delete section
  const deleteSection = useCallback(
    (sectionId: string) => {
      updateReport({
        sections: report.sections.filter((s) => s.id !== sectionId),
      });
    },
    [report.sections, updateReport]
  );

  // Move section
  const moveSection = useCallback(
    (sectionId: string, direction: 'up' | 'down') => {
      const index = report.sections.findIndex((s) => s.id === sectionId);
      if (index === -1) return;

      const newIndex = direction === 'up' ? index - 1 : index + 1;
      if (newIndex < 0 || newIndex >= report.sections.length) return;

      const newSections = [...report.sections];
      [newSections[index], newSections[newIndex]] = [
        newSections[newIndex],
        newSections[index],
      ];

      updateReport({ sections: newSections });
    },
    [report.sections, updateReport]
  );

  // Handle export
  const handleExport = useCallback(
    (format: 'pdf' | 'print' | 'docx') => {
      if (format === 'print') {
        // Trigger print dialog
        window.print();
      }
      onExport?.(format);
    },
    [onExport]
  );

  // Get section contradiction IDs that are in the report
  const sectionContradictionIds = useMemo(() => {
    return new Set(
      report.sections
        .filter((s) => s.type === 'contradiction' && s.referenceId)
        .map((s) => s.referenceId!)
    );
  }, [report.sections]);

  return (
    <div className={`flex flex-col h-full ${className}`} dir="rtl">
      {/* Header */}
      <div className="px-4 py-3 border-b border-slate-700 bg-slate-800/50">
        <div className="flex items-center justify-between mb-3">
          <div className="flex items-center gap-3">
            <FileText className="w-5 h-5 text-blue-400" />
            <input
              type="text"
              value={report.title}
              onChange={(e) => updateReport({ title: e.target.value })}
              className="text-lg font-semibold text-white bg-transparent focus:outline-none focus:ring-1 focus:ring-blue-500 rounded px-2 py-1"
            />
          </div>

          <div className="flex items-center gap-2">
            {/* Preview */}
            <button
              onClick={() => setShowPreview(true)}
              className="flex items-center gap-2 px-3 py-1.5 text-white/60 hover:text-white hover:bg-slate-700 rounded-lg transition-colors"
            >
              <Eye className="w-4 h-4" />
              <span className="text-sm">תצוגה מקדימה</span>
            </button>

            {/* Save */}
            {onSave && (
              <button
                onClick={() => onSave(report)}
                className="flex items-center gap-2 px-3 py-1.5 text-white/60 hover:text-white hover:bg-slate-700 rounded-lg transition-colors"
              >
                <Save className="w-4 h-4" />
                <span className="text-sm">שמור</span>
              </button>
            )}

            {/* Export dropdown */}
            <div className="relative group">
              <button className="flex items-center gap-2 px-3 py-1.5 bg-green-600 hover:bg-green-500 text-white rounded-lg transition-colors">
                <Download className="w-4 h-4" />
                <span className="text-sm">ייצוא</span>
                <ChevronDown className="w-3 h-3" />
              </button>

              <div className="hidden group-hover:block absolute top-full left-0 mt-1 bg-slate-800 border border-slate-700 rounded-lg shadow-xl overflow-hidden">
                <button
                  onClick={() => handleExport('pdf')}
                  className="w-full flex items-center gap-2 px-4 py-2 text-white/80 hover:bg-slate-700 transition-colors"
                >
                  <Download className="w-4 h-4" />
                  <span className="text-sm">PDF</span>
                </button>
                <button
                  onClick={() => handleExport('print')}
                  className="w-full flex items-center gap-2 px-4 py-2 text-white/80 hover:bg-slate-700 transition-colors"
                >
                  <Printer className="w-4 h-4" />
                  <span className="text-sm">הדפסה</span>
                </button>
              </div>
            </div>
          </div>
        </div>

        {/* Stats */}
        <div className="flex items-center gap-4 text-xs text-white/40">
          <span>{report.sections.length} קטעים</span>
          <span>•</span>
          <span>
            {report.sections.filter((s) => s.type === 'contradiction').length} סתירות
          </span>
          <span>•</span>
          <span>עודכן: {new Date(report.updatedAt).toLocaleTimeString('he-IL')}</span>
        </div>
      </div>

      {/* Toolbar */}
      <div className="px-4 py-3 border-b border-slate-700 bg-slate-900/50">
        <AddSectionMenu
          onAdd={addSection}
          contradictions={contradictions}
          reportContradictionIds={sectionContradictionIds}
          onAddContradiction={addContradictionSection}
        />
      </div>

      {/* Sections list */}
      <div className="flex-1 overflow-y-auto p-4 space-y-3">
        {report.sections.length === 0 ? (
          <div className="flex flex-col items-center justify-center h-64 text-center">
            <FolderOpen className="w-12 h-12 text-white/20 mb-3" />
            <p className="text-white/40 text-sm mb-2">הדוח ריק</p>
            <p className="text-white/30 text-xs">
              הוסף קטעים להתחלת בניית הדוח
            </p>
          </div>
        ) : (
          report.sections.map((section, index) => (
            <SectionEditor
              key={section.id}
              section={section}
              onUpdate={(s) =>
                updateSection(section.id, {
                  title: s.title,
                  content: s.content,
                  isVisible: s.isVisible,
                })
              }
              onDelete={() => deleteSection(section.id)}
              onMoveUp={() => moveSection(section.id, 'up')}
              onMoveDown={() => moveSection(section.id, 'down')}
              isFirst={index === 0}
              isLast={index === report.sections.length - 1}
            />
          ))
        )}
      </div>

      {/* Preview modal */}
      {showPreview && (
        <ReportPreview report={report} onClose={() => setShowPreview(false)} />
      )}
    </div>
  );
};

export default ReportBuilder;
